/** Animations **/
@keyframes fade-in-opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes float-up {
  0% {
    opacity: 0;
    transform: scale(0.86);
  }
  25% {
    opacity: 100;
  }
  67% {
    transform: scale(1);
  }
  100% {
    transform: scale(1);
  }
}
/** General **/
html {
  overflow-y: auto;
}

body {
  animation: fade-in-opacity 0.5s;
}

