  padding: 0 !important;
}

/* floating button's bottom offset + height + bottom offset */
.section.has-extra-bottom-padding {
  padding-bottom: 6.5rem;
}

/** Floating home button **/
a.floating-home-button {
  display: flex;
  position: fixed;
  right: 1.5rem;
  bottom: 1.5rem;
  border-radius: 100%;
  background-color: hsl(208deg, 79%, 51%);
  color: hsl(0deg, 0%, 100%);
  width: 3.5rem;
  height: 3.5rem;
  justify-content: center;
  align-items: center;
  transition: background-color 0.25s, color 0.25s;
}
a.floating-home-button:hover {
  background-color: #edf5fd;
  color: hsl(0deg, 0%, 0%);
}
a.floating-home-button > .icon {
  margin-top: -2px;
}

.hero.is-fullheight {
  /* https://github.com/philipwalton/flexbugs#flexbug-3 */
}
.hero.is-fullheight > .hero-body {
  min-height: 100vh;
  height: 100%;
  /* https://github.com/philipwalton/flexbugs#flexbug-2 */
}
.hero.is-fullheight > .hero-body > .container {
  width: 100%;
}